.list {
  display: flex;
  flex-direction: column;
  min-width: 100px;
  min-height: 100px;
  flex: 1;
  background-color: #1f2024;
  overflow: auto;
  padding: 10px;
}
.streamer {
  position: relative;
  cursor: pointer;
  background-color: #0f0f0f;
  padding: 5px;
  margin: 5px 0;
  display: flex;
  align-items: center;
  border-radius: 5px;
}
.streamer-name {
  margin-left: 10px;
}
.chosen {
  color: #fff;
  background-color: #a970ff;
}
.profil-pic {
  width: 35px;
  height: 35px;
  border-radius: 100%;
  margin: 3px;
}
