.main {
  position: relative;
  font-family: Helvetica, sans-serif;
  font-size: 30px;
  color: #fff;
  padding: 0 10px;
  background-color: #1f2024;
}
.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #1f2024;
}
.lists {
  font-size: 23px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  background-color: #1f2024;
}
.title {
  color: #fff;
  font-size: 23px;
  margin: 10px;
}
.listContainer {
  display: flex;
  flex-direction: column;
  min-width: 100px;
  min-height: 100px;
  flex: 1;
  background-color: #1f2024;
  overflow: auto;
  height: 90vh;
}
.separator {
  width: 10px;
}
.full {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.row {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.margin {
  margin: 0 10px;
}
.height {
  height: 10vh;
}
.notifications-container {
  cursor: pointer;
  min-width: 32px;
  height: 32px;
  border-radius: 50%;
  background-color: #a970ff;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 10px;
}
.notifications {
  width: 22px;
  height: 22px;
}
.title-count {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}
::-webkit-scrollbar {
  width: 10px;
  background-color: #1f2024;
}
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.1);
  background-color: #0f0f0f;
  border-radius: 10px;
}
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: #a970ff;
}
